import React from "react"
import { setMessageLog } from "../reducers/messageLogReducer"
import { connect } from "react-redux"
import { idGenerator } from "../utils/idGenerator"

const MessageLog = ({ log, setMessageLog }) => {
  return (
    <div>
      <ul>
        {log.map(message => (
          <li key={idGenerator()}>{message}</li>
        ))}
      </ul>
    </div>
  )
}

const mapStateToProps = state => ({
  log: state.messageLogReducer
})
const mapDispatchToProps = {
  setMessageLog
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageLog)
