import React from "react"
import { setMessage } from "../reducers/messageReducer"
import { addMessageToLog } from "../reducers/messageLogReducer"
import { connect } from "react-redux"

const MessagePrompt = ({ socket, message, setMessage, addMessageToLog }) => {
  const handleMessageSubmit = event => {
    event.preventDefault()
    addMessageToLog(message)
    socket.emit("SENT_MESSAGE", message)
    setMessage("")
  }
  const handleMessageChange = event => {
    console.log("event:", event.target.value)
    setMessage(event.target.value)
  }

  return (
    <div>
      <form>
        <input
          type="text"
          value={message}
          onChange={e => handleMessageChange(e)}
        ></input>
        <button onClick={e => handleMessageSubmit(e)}>send</button>
      </form>
    </div>
  )
}

const mapStateToProps = state => ({
  message: state.messageReducer
})
const mapDispatchToProps = {
  setMessage,
  addMessageToLog
}

export default connect(mapStateToProps, mapDispatchToProps)(MessagePrompt)
